import { Link } from 'gatsby';
import React from 'react';
import Footer from '../components/Footer';

import SiteHeader from '../components/SiteHeader';

import './layout.css';
import ThemeContext from '../context/ThemeContext';
import { rhythm } from '../utils/typography';
import styled from 'styled-components';
import ReactCSSTransitionReplace from 'react-css-transition-replace';

const Header = styled.h1`
  margin-top: 0;
  font-weight: 300;
  font-size: 1.7rem;
  transition: all 0.3s ease;

  ${props => {
    if (props.darkMode) {
      return `
  
  a, a:visited {
    color: #fff !important;
  }`;
    }
  }};
`;

const StyledLink = styled(Link)`
  box-shadow: none;
  text-decoration: none;
  color: inherit;
`;

const FlexContainer = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  transition: all 0.3s ease;
  h2 > a {
    box-shadow: none;
  }
  ${props => {
    if (props.darkMode) {
      return `background-color: #2f3640;
  color: #fff;
  
  a, a:visited {
    color: rgb(73, 223, 255);
  }
  
  hr {
    background: #505050;
  }
  
  blockquote: {
    color: inherit;
    border-color: inherit;
  }
  `;
    }
  }}
`;

const MainContainer = styled.div`
  flex: 1;
`;

const LeftContainer = styled.div`
  @media (min-width: 735px) {
    margin: 0 10% 10% 10%;
  }
  margin: 0 0% 1% 0;
  max-width: ${rhythm(24)};
  padding: 0 ${rhythm(7 / 8)} ${rhythm(3 / 4)};
`;

class Layout extends React.Component {
  render() {
    const { location, title, children } = this.props;

    return (
      <ThemeContext.Consumer>
        {themeObj => (
          <ReactCSSTransitionReplace
            transitionName="cross-fade"
            transitionEnterTimeout={3000}
            transitionLeaveTimeout={3000}
          >
            <FlexContainer darkMode={themeObj.darkMode}>
              <SiteHeader title={title} themeObj={themeObj} />
              <MainContainer>
                <LeftContainer>{children}</LeftContainer>
              </MainContainer>
              <Footer darkMode={themeObj.darkMode} />
            </FlexContainer>
          </ReactCSSTransitionReplace>
        )}
      </ThemeContext.Consumer>
    );
  }
}

export default Layout;
